
import {
  defineComponent,
  ref,
  watch,
  watchEffect,
} from 'vue';

export default defineComponent({
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  setup(props) {
    const rate = ref();

    watch(rate, (val) => {
      localStorage.setItem(props.id.toString(), val);
    });

    watchEffect(() => {
      const value = localStorage.getItem(props.id.toString());

      if (value) {
        rate.value = value;
      }
    });

    return { rate };
  },
});
